.App {
  display: grid;
  margin: 0 auto;
  max-width: 1200px;
  grid-template-areas: "h h"
  "nav cnt";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
}



.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.wrapper-content {
  grid-area: cnt;
  background-color:rgb(216, 211, 219);
}